import { createSlice } from '@reduxjs/toolkit'

export const randomName = createSlice({

    name: 'textMaker',
    initialState: {value: ''},
    reducers: {

        oskar: state => {
            state.value = '';
        },

        danik: state => {
            state.value = '';
        },

        jenya: state => {
            state.value = '';
        }

    }

})

export const { oskar, danik, jenya } = randomName.actions

export default randomName.reducer